.chart-table {
	display: flex;
	flex-wrap: wrap;

	border-top: rem(1) solid var(--border-color);
	border-bottom: rem(1) solid var(--border-color);

	&__item {
		padding: rem(20) rem(25);

		&-top {

			.name {
				font-weight: var(--font-w-sm);
				color: var(--slate-two);
			}

			.first {
				font-size: rem(22);
				letter-spacing: 0.29px;
			}

			.last {
				font-size: rem(18);
				color: var(--battleship-grey);
				letter-spacing: 0.24px;
			}

		}

		&-bottom {
			span {
				font-size: rem(12);
				color: var(--battleship-grey);
			}

			.chart-table__canvas {
				height: rem(60) !important;
				margin-top: rem(28);
			}
		}
	}
}

.chart-table-3 {

	.chart-table__item {
		width: 33.333%;

		&:first-child,
		&:nth-child(2),
		&:nth-child(4),
		&:nth-child(5) {
			border-right: rem(1) solid var(--border-color);
		}

		&:nth-child(4),
		&:nth-child(5),
		&:last-child {
			border-top: rem(1) solid var(--border-color);
		}
	}

}