.filter_content {
	position: absolute;

	width: rem(253);
	border-radius: rem(5);
	box-shadow: 0 6px 16px 0 rgba(136, 146, 163, 0.2),
	0 2px 8px 0 rgba(136, 146, 163, 0.2);
	border: rem(1) solid var(--border-color);

	z-index: 100;

	background-color: #f7fafc;

	display: none;

	&__header {
		padding: rem(10);

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		background-color: white;

		button {
			height: rem(24);
			padding: 0 rem(8);
		}

		b {
			font-weight: var(--font-w-sm);
			color: var(--slate-two);
		}
	}

	&__section {
		padding: rem(10);
		border-top: rem(1) solid var(--border-color);
		border-bottom: rem(1) solid var(--border-color);

		display: flex;
		flex-wrap: wrap;
		align-items: center;

		label {
			cursor: pointer;
			margin-left: rem(10);
			color: var(--slate-two);
			font-weight: var(--font-w-sm);
		}
	}

	&__fields {
		padding: rem(10);
		background-color: white;

		.light-text {
			color: #8892a4;
			letter-spacing: 0.6px;
		}
		
		.field-set {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			label {
				font-weight: var(--font-w-sm);
				color: var(--slate-two);
			}
		}

		.button {
			padding: 0 rem(3);
			height: rem(26);
			select {
				width: 100%;
				height: rem(24);
				padding: 0 rem(7);
				position: relative;
				z-index: 2;

				font-size: rem(14);
				color: var(--slate-two);
				font-weight: var(--font-w-sm);
			}
		}

		.filter-select-days {
			width: rem(73);
			
		}

		.field {
			height: rem(26);
			width: rem(60);
			border-radius: rem(5);
			background-color: white;
			border: rem(1) solid var(--border-color);
			padding: 0 rem(7);
			

			font-size: rem(14);
		}
	}
}

.filter_content.active {
	display: block;
}