.table {

	&__header {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		margin-bottom: rem(10);
	}

	&__nav {
		margin-bottom: rem(26);

		ul {
			position: relative;
			display: flex;
			flex-wrap: wrap;

			&::before {
				content: "";
				position: absolute;
				left: 0;
				bottom: rem(-8);
				width: 100%;
				height: rem(1);
				background-color: var(--border-color);
				z-index: -1;
			}

			li {
				margin-right: rem(24);

				&:last-child {
					margin-right: 0;
				}

				a {
					font-weight: var(--font-w-sm);
					color: var(--battleship-grey);

					padding-bottom: rem(7);

					border-bottom: rem(2) solid transparent;

					&:hover {
						color: var(--cornflower);

						border-color: var(--cornflower);
					}
				}
			}

			li.active {

				a {
					color: var(--cornflower);
					border-color: var(--cornflower);
				}

			}
		}
	}

	&__rows {
		display: flex;
		flex-direction: column;

		.row {
			position: relative;
			padding: rem(12) 0;

			display: flex;
			flex-wrap: wrap;

			border-bottom: rem(1) solid var(--border-color);

			&:hover {
				background-color: #f7fafc;

				.other_list {
					opacity: 1;
				}
			}

			.other_list {
				position: absolute;
				top: rem(11);
				right: rem(8);

				padding: 0 rem(8);
				height: rem(24);

				opacity: 0;
			}

			.link {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}

			input {
				position: relative;
				z-index: 2;
			}

			.col {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				b {
					font-weight: var(--font-w-sm);
				}

				span {
					color: #3e4559;
					letter-spacing: 0.5px;
				}
			}

			.col.w-3 {
				width: 3%;
			}
			.col.w-30 {
				width: 30%;
			}
			.col.w-35 {
				width: 35%;
			}
			.col.w-16 {
				width: 16%;
			}
		}

		.row.first {
			b {
				font-size: rem(12);
				letter-spacing: 0.5px;
			}
		}
	}

	&__result {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		margin-top: rem(20);
		margin-left: rem(32);
		margin-right: rem(16);

		.first {
			b, span {
				color: #3e4559;
			}

			b {
				margin-right: rem(2);
			}
		}
	}

}