:root {
	--font-default: 'Source Sans Pro';
	--font-w-l: 300;
	--font-w-sm: 600;

	// --dark-indigo: #0a2540;
	// --slate: #425566;
	--cornflower: #635cff;
	--cornflower-h: #4b44e5;
	--mango: #feb524;
	--strong-pink: #fe249d;
	--aqua-marine: #50e3c2;
	--bluey-grey-two: #8892a3;
	--light-tan: #f9e6ba;
	--light-sage: #cbf4ca;
	--dark: #191f36;
	--battleship-grey: #6a7486;

	--border-color: #e3e8ee;

	--aside-bg: #f7fafc;


	// --purpleish-pink: #e66ecb;
	// --pale-grey: #fcfafb;
	// --pale-grey-h: #f4f4f4;
	// --bluey-grey: #8c9fb1;
	// --orange-pink: #ff6a5c;
	// --orange-pink-h: #ff5a4b;
	// --slate-two: #3c4357;
	// --steel: #738096;

	--w-left-column: 269px;

	--w-container: 1170px;
}

* {
	outline: none;
	border: 0;

	transition: all 300ms ease;
}

html,
body {
	position: relative;
	overflow-x: hidden;
	height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

input {
	margin: 0;
}

button {
	background-color: transparent;
	font-size: rem(16);
	padding: 0;
}

a {
	text-decoration: none;
}

p {
	color: var(--slate);
}

h1 {
	font-size: rem(30);
	color: var(--dark);
	font-weight: var(--font-w-sm);
}

h2,
h3 {
	font-size: rem(43);
	line-height: rem(49);
	letter-spacing: -0.25px;
	color: var(--dark-indigo);
}

li {
	list-style: none;
}

body {
	position: relative;
	font-size: rem(15);
	font-family: var(--font-default);
	color: var(--dark);
	// height: 100%;
	overflow-x: hidden;
	// -webkit-overflow-scrolling: touch;
}

.container {
	min-height: 100%;
	display: flex;
	// flex-wrap: wrap;
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}

.h-full {
	height: 100%;
}

.block {
	display: block;
}

.flex {
	display: flex;
}

.flex-wrap {
	flex-wrap: wrap;
}

.flex-col {
	flex-direction: column;
}

.flex-end {
	justify-content: flex-end;
}

.flex-center {
	justify-content: center;
}

.flex-between {
	justify-content: space-between;
}

.items-center {
	align-items: center !important;
}

.items-start {
	align-items: flex-start !important;
}

.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}

.text-right {
	text-align: right;
}

.text-left {
	text-align: left;
}


