.aside {
	background-color: var(--aside-bg);
	padding: rem(20);

	flex: 0 0 var(--w-left-column);
	max-width: var(--w-left-column);

	min-height: 100%;

	border-right: rem(1) solid var(--border-color);

	&__header {
		margin-bottom: rem(36);

		&-selected {

			button {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				img {
					width: rem(28);
					height: rem(28);
					margin-right: rem(8);
				}

				span {
					font-weight: var(--font-w-sm);
					font-size: rem(17);
					letter-spacing: -0.23px;

					i {
						margin-left: rem(16);
						font-size: rem(6);
						vertical-align: middle;
					}
				}
			}
		}
	}

	&__nav {
		padding-left: rem(8);

		ul {

			li {
				display: flex;
				align-items: center;
				margin-bottom: rem(14);

				a {
					letter-spacing: 0.2px;
					display: flex;
					align-items: center;
					transition: all 300ms ease;

					&:hover {
						color: var(--cornflower);
						text-shadow: 0 0 .65px var(--cornflower),
						0 0 .65px var(--cornflower);
					}

					.ic {
						margin-right: rem(14);
						
					}
				}
				[data-item="home"] {
					&:hover {
						.ic {
							.ic_path_1 {
								fill: #ADAAFD !important;
							}

							.ic_path_2 {
								fill: #ADAAFD !important;
							}
						}
					}
				}
				[data-item="customers"] {
					&:hover {
						.ic {
							#ic1_1 {
								fill: var(--cornflower) !important;
							}
						}
					}
				}
				[data-item="billing"] {
					&:hover {
						.ic {
							.ic_path_1 {
								fill: #ADAAFD !important;
							}
							.ic_path_2 {
								fill: var(--cornflower) !important;
							}
						}
					}
				}

				[data-item="coupons"] {
					&:hover {
						.ic {
							.ic_path_1 {
								fill: var(--cornflower);
							}

							.ic_path_2 {
								fill: #ADAAFD;
							}
						}
					}
				}
				[data-item="taxes"] {
					.ic {
						position: relative;
						width: rem(14);
						height: rem(14);

						&::before, &::after {
							content: "";
							position: absolute;
							width: rem(6);
							height: rem(6);
							border-radius: 50%;
							// margin: 0 2px 2px 0;
							border: solid rem(1.5) #a4acba;
						}

						&::before {
							top: 0;
							left: 0;
						}

						&::after {
							right: 0;
							bottom: 0;
						}
						
						span {
							position: absolute;
							left: rem(7);
							width: rem(2);
							height: rem(14);
							transform: rotate(45deg);
							background-color: #50576b;
						}
					}

					&:hover {
						&::before, &::after {
							border-color: var(--cornflower);
						}

						span {
							background-color: var(--cornflower);
						}
					}
				}
				[data-item="reports"] {
					&:hover {
						.ic {
							.ic_path_1 {
								fill: #ADAAFD;
							}

							.ic_path_2 {
								fill: var(--cornflower);
							}
						}
					}
				}
				[data-item="settings"] {
					&:hover {
						.ic {
							.ic_path_1 {
								fill: var(--cornflower);
							}
						}
					}
				}
				[data-item="account"] {
					&:hover {
						.ic {
							.ic_path_1 {
								fill: #ADAAFD;
							}

							.ic_path_2 {
								fill: var(--cornflower);
							}
						}
					}
				}
			}

			li.active {
				a {
					color: var(--cornflower);
					text-shadow: 0 0 .65px var(--cornflower),
					0 0 .65px var(--cornflower);
				}

				[data-item="home"] {
					.ic {
						.ic_path_1 {
							fill: #ADAAFD !important;
						}

						.ic_path_2 {
							fill: #ADAAFD !important;
						}
					}
				}

				[data-item="customers"] {
					.ic {
						#ic1_1 {
							fill: var(--cornflower) !important;
						}
					}
				}

				[data-item="billing"] {
					.ic {
						.ic_path_1 {
							fill: #ADAAFD !important;
						}

						.ic_path_2 {
							fill: var(--cornflower) !important;
						}
					}
				}

				[data-item="coupons"] {
					.ic {
						.ic_path_1 {
							fill: var(--cornflower);
						}

						.ic_path_2 {
							fill: #ADAAFD;
						}
					}
				}

				[data-item="taxes"] {
					.ic {
						position: relative;
						width: rem(14);
						height: rem(14);

						&::before,
						&::after {
							content: "";
							position: absolute;
							width: rem(6);
							height: rem(6);
							border-radius: 50%;
							// margin: 0 2px 2px 0;
							border: solid rem(1.5) #a4acba;
						}

						&::before {
							top: 0;
							left: 0;
						}

						&::after {
							right: 0;
							bottom: 0;
						}

						span {
							position: absolute;
							left: rem(7);
							width: rem(2);
							height: rem(14);
							transform: rotate(45deg);
							background-color: #50576b;
						}
					}

					// &:hover {

					// 	&::before,
					// 	&::after {
					// 		border-color: var(--cornflower);
					// 	}

					// 	span {
					// 		background-color: var(--cornflower);
					// 	}
					// }
				}

				[data-item="reports"] {
					.ic {
						.ic_path_1 {
							fill: #ADAAFD;
						}

						.ic_path_2 {
							fill: var(--cornflower);
						}
					}
				}

				[data-item="settings"] {
					.ic {
						.ic_path_1 {
							fill: var(--cornflower);
						}
					}
				}

				[data-item="account"] {
					.ic {
						.ic_path_1 {
							fill: #ADAAFD;
						}

						.ic_path_2 {
							fill: var(--cornflower);
						}
					}
				}
			}
		}
	}
}