.header {
	height: rem(55);
	border-bottom: rem(1) solid var(--border-color);

	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;

	&__search {
		margin-left: rem(40);
		margin-right: rem(40);
		flex-grow: 1;

		.field__set {
			display: flex;
			// flex-wrap: wrap;
			width: 100%;
			align-items: center;

			.ic {
				margin-right: rem(5);
				vertical-align: baseline;
				margin-top: rem(4);
			}

			.field__search {
				width: 100%;
				height: rem(24);
				font-size: rem(15);
				letter-spacing: 0.25px;
				color: var(--battleship-grey);

				@include placeholder {
					color: var(--battleship-grey);
				}
			}
		}
	}

	&__right {
		margin-right: rem(40);
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		.button__item {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			&:hover {
				.ic {
					svg {
						fill: var(--cornflower);

						path, g {
							transition: all 300ms ease;
							fill: var(--cornflower);
						}
					}
				}
				span {
					color: var(--cornflower);
				}
			}

			.ic {
				padding-top: rem(4);
			}

			span {
				margin-left: rem(8);
				letter-spacing: 0.6px;
				color: #8892a4;

				transition: all 300ms ease;
			}
		}
	}
}